import { checkout } from "../services/api";
import stripe from "../services/stripe";
import {
  getProvider,
  getInstance,
  getAccounts,
  sendAsset,
} from "../services/substrate";
import config from "../config";
import { loadScript } from "../utils/tools";

export default {
  components: {
    pricePerLesson: () => import('../components/includes/PricePerLesson.vue'),
    Spot: () => import('../components/includes/Spot.vue'),
    Button: () => import ('../ui/button/index.vue')
  },
  data() {
    return {
      isReady: false,
      error: null,
      api: null,
      unsubscribe: null,
      balance: 0,
      accounts: [],
      accountDefault: '',
      status: false,
      schedulingIsPaid: false,
      schedulingPaymentTxSent: false,
    };
  },
  async created() {
    try {
      const provider = getProvider();
      provider.on("error", () => {
        this.error = "Disconnected provider";
        this.isReady = false;
      });
      provider.on("connect", () => {
        this.isReady = true;
      });
      this.api = await getInstance();
      this.accounts = await getAccounts(this.api);
      
      if(this.accounts) {
        this.accountDefault = this.accounts[0].address;
      }

      if (this.accounts.length === 0) {
        this.error = "Not found account";
      }
      this.isReady = true;
      this.status = true;

    } catch (error) {
      this.error = error.message;
    }
  },
  destroyed() {
    if (this.unsubscribe) {
      this.unsubscribe();
    }
  },
  watch: {
    status: async function(newValue, oldValue) {
      if (oldValue === false && newValue === true) {
        await loadScript(
          "https://static.hsappstatic.net/MeetingsEmbed/ex/MeetingsEmbedCode.js"
        );
      }
    },
    account: function(newValue, oldValue) {
      this.onChange({
        name: "account",
        newValue: newValue,
        oldValue: oldValue
      });
    }
  },

  computed: {
    checkedAccount: {
      get () {
        return this.$store.state.checkedAccounts.includes(this.$store.state.accountActive)
      },
      set (value) {
        this.$store.commit('setAccountChecked', value)
      }
    },
    account: {
      get () {
        return this.$store.state.accountActive ? this.$store.state.accountActive : this.accountDefault
      },
      set (value) {
        this.$store.commit('setAccountActive', value)
      }
    },
    email: {
      get () {
        return this.$store.state.email
      },
      set (value) {
        this.$store.commit('setEmail', value)
      }
    }
  },

  methods: {
    addressShort(address) {
      return address.slice(0, 6) + "..." + address.slice(-4);
    },
    async onChange({ name, newValue }) {
      if (name === "account") {
        if (this.unsubscribe) {
          this.unsubscribe();
        }
        this.unsubscribe = await this.api.query.assets.account(
          config.ID_ASSET,
          newValue,
          (info) => {
            console.log("info:", info)
            try {
              this.balance = info.value.balance.toNumber();
            } catch (e) {
              this.balance = 0;
            }
          }
        );
      }
    },
    handleSubmit() {
      if (this.email && this.account) {
        this.checkout(this.account, this.email);
      }

    },
    async checkout(account, email) {
      this.proccess = true;
      try {
        const session = await checkout({
          account: account,
          email: email
        });
        const r = await stripe.redirectToCheckout({ sessionId: session.id });
        if (r.error) {
          console.error(r.error.message);
        }
      } catch (error) {
        console.log(error);
      }
      this.proccess = false;
    },
    async spendSpot() {
      const SPOT_STATEMINE_ASSET_ID = 1441
      const STRELKA_SPOT_2 = "GJLkKWzoLPjhdWwMWUu1K2Tmp69CRnsB14WhDjMmkF4X4et"
      const success = await sendAsset(
        this.account, STRELKA_SPOT_2, SPOT_STATEMINE_ASSET_ID, 1, this.onSpotSpendingSuccess
      )
      if (!success) {
        console.log("Spot token not spent")
        return
      }
      this.schedulingPaymentTxSent = true
    },
    async onSpotSpendingSuccess() {
      console.log("onSpotSpendingSuccess")
      this.schedulingIsPaid = true
    }
  }
};
